sibilidades de elección para durar una vida plena de sentido, según el momento de curso de vida en el que se desarrollan, las características y condiciones poblacionales que los reconocen, y los territorios específicos que habitan y construyen subjetivamente, asegurando, para todo ser humano, libertad, bienestar y dignidad.")]),_c('p',[_vm._v("El despliegue fundamentado en derechos humanos tiene una estrecha relación con la salud mental, toda vez que dichos inconvenientes afectan la esperanza de vida, los años de instrucción esperados y el Ingreso Nacional Bruto "),_c('i',[_vm._v("per cápita")]),_vm._v(", provocando resultados negativos y consecuencias de la pobreza; por otra parte, las personas con dificultades mentales deben confrontar numerosos obstáculos para el ingreso a la educación, a las conveniencias de empleo y otras fuentes de concepción de ingresos, debido a la señalación, exclusión y discriminación que históricamente han vivido.")])]),_c('div',{staticClass:"col-lg-3"},[_c('figure',[_c('img',{staticClass:"m-auto",staticStyle:{"width":"150px"},attrs:{"src":require("@/assets/curso/tema3/img7.png"),"alt":"Imagen decorativa"}})])])])]),_c('div',{staticClass:"py-4 py-md-5",attrs:{"titulo":"Ciclo de vida","icono":require('@/assets/curso/tema3/img4.svg')}},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-8 mb-4 mb-lg-0"},[_c('p',{staticClass:"mb-0"},[_vm._v("Deduce del vínculo trazado entre desarrollo y derechos, el encausamiento de curso de vida se considera importante desde la aprobación de las trayectorias de vida, los sucesos y transiciones en el vivir de los seres humanos. Admite a su vez, registrando ventanas de oportunidad que inciden en la vida cotidiana de los sujetos en el marco de sus relaciones, la toma de decisiones y su proyecto de vida. Al extender en vínculo con los derechos, y la aceptación del curso de vida de los sujetos individuales y colectivos, constituye un apoyo clave para la comprensión y abordaje tanto de la promoción y sostenimiento de la salud mental como el abordaje de los problemas e inconvenientes mentales; toda vez que es sabido que una misma patología puede originar sintomatología de variadas características en niños, niñas y adolescentes, adultos, o adultos mayores, como ocurre en la depresión o en el riesgo suicida.")])]),_c('div',{staticClass:"col-lg-3"},[_c('figure',[_c('img',{staticClass:"m-auto",staticStyle:{"width":"150px"},attrs:{"src":require("@/assets/curso/tema3/img8.png"),"alt":"Imagen decorativa"}})])])])]),_c('div',{staticClass:"py-4 py-md-5",attrs:{"titulo":"Género","icono":require('@/assets/curso/tema3/img5.svg')}},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-8 mb-4 mb-lg-0"},[_c('p',[_vm._v("Es una distribución social que define la construcción de roles, apreciaciones, estándares e imaginarios asociados a lo masculino y lo femenino, y los enlaces de poder que de éstos se desatan con otras formas de desigualdad. Estas edificaciones sociales difieren entre sociedades, culturas y se transforman en el tiempo y parten de probabilidades colectivas de género que se modifican dependiendo de la condición de clase. Los apartamientos por razones de género permean las estructuras sociales, culturales, económicas y políticas; y tienen impactos individuales, comunitarios y colectivos. Se destaca el requerimiento de que en la atención en salud mental se reconozcan las identidades de género y las orientaciones sexuales, como también los roles de género desde una perspectiva de igualdad.")]),_c('p',{staticClass:"mb-0"},[_vm._v("Las obligaciones en salud mental son desiguales cuando se examina este enfoque. Es así que en Colombia el mayor riesgo de depresión, ansiedad y conducta suicida lo exponen las mujeres, en tanto que el riesgo de consumo de sustancias psicoactivas y el suicidio consumado es mayor para los hombres.")])]),_c('div',{staticClass:"col-lg-3"},[_c('figure',[_c('img',{staticClass:"m-auto",staticStyle:{"width":"150px"},attrs:{"src":require("@/assets/curso/tema3/img9.png"),"alt":"Imagen decorativa"}})])])])]),_c('div',{staticClass:"py-4 py-md-5",attrs:{"titulo":"Diferencial","icono":require('@/assets/curso/tema3/img6.svg')}},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-8 mb-4 mb-lg-0"},[_c('p',[_vm._v("Perceptibilidad de la importancia de las individualidades de los sujetos, considerando su situación, y su contexto con las variables culturales, sociales y políticas que les son inherentes. Implica el conocimiento de condiciones constitutivas de discapacidad, edad, etnia, así como sociales, culturales, religiosas, políticas y económicas, las afectaciones por violencias (social y política), de ubicación geográfica y la condición legal, entre otras. Del mismo modo, fomenta la totalidad de los derechos, lo multicultural, lo étnico y promueve la lectura en contexto. En este sentido, al orientar el enfoque diferencial se observa la importancia de la relación población – territorio, como categorías que promueven la formulación, implementación y evaluación de políticas que tengan como punto de inicio a las personas de forma individual o colectiva de derechos, como las condiciones poblacionales y territoriales.")]),_c('p',{staticClass:"mb-0"},[_vm._v("Junto con el desarrollo del enfoque diferencial, se requiere el enfoque interseccional entendido como la comprensión de los procesos complejos que derivan de la interacción de factores políticos, sociales, culturales, económicos y simbólicos en cada contexto.")])]),_c('div',{staticClass:"col-lg-3"},[_c('figure',[_c('img',{staticClass:"m-auto",staticStyle:{"width":"150px"},attrs:{"src":require("@/assets/curso/tema3/img10.png"),"alt":"Imagen decorativa"}})])])])]),_c('div',{staticClass:"py-4 py-md-5",attrs:{"titulo":"Psicosocial","icono":require('@/assets/curso/tema3/img7.svg')}},[_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-8 mb-4 mb-lg-0"},[_c('p',{stat